<template lang="pug">
  div#joinus
    div.container
      h1.joinus__title.section__title {{ serviceKo }} .
      p.section__subtitle
        | {{ serviceKo }}    .
        br
        |   ,   .
        br
        span.joinus__title--copyEmail(
          @click="toast"
          v-clipboard:copy="mailTo"
          v-tooltip.bottom="{content: '   ', delay: {show: 500, hide: 100}}"
        )   

      div.global__cta
        a.global__cta--link(
          :href="`mailto:${mailTo},${companyMailTo}`"
        )
          button.global__cta--btn(
            v-tooltip.bottom="{content: '   ', delay: {show: 500, hide: 100}}"
          ) 
</template>

<script>
import {globalVar} from '@/globalVar'

export default {
  name: 'joinus',

  data () {
    return {
      companyMailTo: globalVar.companyMailTo,
      baseURI: globalVar.ceoWebsiteUrl,
      serviceEn: globalVar.serviceEn,
      serviceKo: globalVar.serviceKo,
      mailTo: globalVar.mailTo,
    }
  },

  methods: {
    toast () {
      this.$toasted.show('  ', {
        theme: "primary",
        position: "bottom-center",
        duration : 2000
      })
    }
  },

}
</script>

<style lang="scss">
#joinus {
  border-top: 1px solid $texteee;

  .container {
    .section__subtitle {
      margin-bottom: 0;

      .joinus__title--copyEmail {
        cursor: pointer;
        color: $brand-pink;
        display: inline-block;
        @include transition(all 0.25s ease);

        &:hover {
          opacity: 0.5;
        }
      }
    }

    .global__cta--btn {
      color: #fff;
      background-color: $brand-pink;

      &:hover {
        // opacity: .5;
        color: $brand-pink;
        background-color: #fff;
      }
    }
  }
}
</style>



